// ERP 스타일 레이아웃
.main-container {
  display: flex;
  flex-direction: row;
  min-height: calc(100vh - 96px);
  background: var(--bg-secondary);
  transition: background-color 0.3s ease;
  position: relative;

  app-edu-left-menu {
    position: fixed;
    left: 0;
    top: 96px;
    width: 280px;
    height: calc(100vh - 96px);
    z-index: 1000;
  }

  .store-main-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    transition: margin-left 0.3s ease;

    &.menu-open {
      margin-left: 280px;
    }

    .content-section {
      flex: 1;
      padding: 24px;
      background: var(--bg-primary);
    }
  }
}

// Material Tabs 스타일 (스토어 테마 적용)
::ng-deep {
  .mat-mdc-tab-group {
    margin-bottom: 32px;
  }

  .mat-mdc-tab-header {
    background: var(--bg-primary);
    border-radius: 12px;
    padding: 8px;
    margin-bottom: 24px;
    box-shadow: 0 2px 8px var(--shadow);
  }

  .mat-mdc-tab-labels {
    gap: 8px;
  }

  .mat-mdc-tab {
    min-width: 160px;
    padding: 12px 24px;
    border-radius: 8px;
    transition: all 0.3s ease;

    .mdc-tab__text-label {
      font-size: 15px;
      font-weight: 600;
      letter-spacing: 0.3px;
      transition: color 0.3s ease;
    }

    &:hover {
      background: var(--bg-secondary);
    }
  }

  // 라이트 모드 (주황색 텍스트)
  [data-theme='light'] {
    .mat-mdc-tab {
      .mdc-tab__text-label {
        color: var(--text-secondary);
      }

      &.mdc-tab--active {
        .mdc-tab__text-label {
          color: #ff6f00;
          font-weight: 700;
        }
      }

      &:hover:not(.mdc-tab--active) {
        .mdc-tab__text-label {
          color: #ff9800;
        }
      }
    }

    // 탭 하단 인디케이터 (언더라인) - 숨김
    .mdc-tab-indicator__content--underline {
      display: none !important;
    }

    .mat-mdc-tab-indicator {
      display: none !important;
    }

    .mat-mdc-tab-header-pagination-chevron {
      border-color: #ff9800;
    }

    .mat-mdc-tab-body-content {
      padding: 24px;
      background: var(--bg-primary);
      border-radius: 12px;
      box-shadow: 0 2px 8px var(--shadow);
    }
  }

  // 다크 모드 (녹색 텍스트)
  [data-theme='dark'] {
    .mat-mdc-tab {
      .mdc-tab__text-label {
        color: var(--text-secondary);
      }

      &.mdc-tab--active {
        .mdc-tab__text-label {
          color: #4caf50;
          font-weight: 700;
        }
      }

      &:hover:not(.mdc-tab--active) {
        .mdc-tab__text-label {
          color: #66bb6a;
        }
      }
    }

    // 탭 하단 인디케이터 (언더라인) - 숨김
    .mdc-tab-indicator__content--underline {
      display: none !important;
    }

    .mat-mdc-tab-indicator {
      display: none !important;
    }

    .mat-mdc-tab-header-pagination-chevron {
      border-color: #4caf50;
    }

    .mat-mdc-tab-body-content {
      padding: 24px;
      background: var(--bg-primary);
      border-radius: 12px;
      box-shadow: 0 2px 8px var(--shadow);
    }
  }

  // Ink bar (선택된 탭 하단 바)
  .mat-mdc-tab-header .mat-mdc-tab-header-pagination-after,
  .mat-mdc-tab-header .mat-mdc-tab-header-pagination-before {
    display: none;
  }
}

// 필터 탭
.filter-tabs {
  display: flex;
  gap: 12px;
  margin-bottom: 24px;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--border-color);
  flex-wrap: wrap;

  .tab-btn {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 12px 24px;
    background: var(--bg-secondary);
    border: 1px solid var(--border-color);
    border-radius: 8px;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 14px;
    color: var(--text-secondary);

    mat-icon {
      font-size: 20px;
      width: 20px;
      height: 20px;
    }

    &:hover {
      background: var(--bg-primary);
      transform: translateY(-2px);
    }

    // 라이트 모드 (주황색)
    [data-theme='light'] & {
      &:hover {
        border-color: #ff9800;
      }

      &.active {
        background: linear-gradient(135deg, #ff9800 0%, #ff6f00 100%);
        color: white;
        border-color: #ff9800;
        box-shadow: 0 4px 12px rgba(255, 152, 0, 0.3);

        mat-icon {
          color: white;
        }
      }
    }

    // 다크 모드 (녹색)
    [data-theme='dark'] & {
      &:hover {
        border-color: #4caf50;
      }

      &.active {
        background: linear-gradient(135deg, #4caf50 0%, #2e7d32 100%);
        color: white;
        border-color: #4caf50;
        box-shadow: 0 4px 12px rgba(76, 175, 80, 0.3);

        mat-icon {
          color: white;
        }
      }
    }
  }
}

// Shimmer 테이블
.shimmer-table {
  .shimmer-row {
    display: grid;
    grid-template-columns: 1fr 2fr 1.5fr 1fr 1fr 1fr 1fr;
    gap: 16px;
    padding: 16px;
    border-bottom: 1px solid var(--border-color);

    .shimmer-cell {
      display: flex;
      align-items: center;
    }

    .shimmer-circle {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: linear-gradient(
        90deg,
        var(--bg-secondary) 0%,
        var(--bg-tertiary, #f0f0f0) 50%,
        var(--bg-secondary) 100%
      );
      background-size: 200% 100%;
      animation: shimmer 1.5s infinite ease-in-out;
    }

    .shimmer-line {
      height: 16px;
      background: linear-gradient(
        90deg,
        var(--bg-secondary) 0%,
        var(--bg-tertiary, #f0f0f0) 50%,
        var(--bg-secondary) 100%
      );
      background-size: 200% 100%;
      animation: shimmer 1.5s infinite ease-in-out;
      border-radius: 4px;

      &.long { width: 100%; }
      &.medium { width: 70%; }
      &.short { width: 50%; }
    }
  }
}

@keyframes shimmer {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

// 테이블 컨테이너
.users-table-container {
  background: var(--bg-primary);
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px var(--shadow);
}

// 빈 상태
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  text-align: center;

  .empty-icon {
    font-size: 64px;
    margin-bottom: 16px;
    opacity: 0.5;
  }

  h3 {
    font-size: 20px;
    font-weight: 600;
    color: var(--text-primary);
    margin: 0 0 8px 0;
  }

  p {
    font-size: 14px;
    color: var(--text-secondary);
    margin: 0;
  }
}

// 테이블
.users-table {
  width: 100%;
  border-collapse: collapse;

  thead {
    background: var(--bg-secondary);
    
    tr {
      border-bottom: 2px solid var(--border-color);
    }

    th {
      padding: 16px;
      text-align: left;
      font-size: 12px;
      font-weight: 600;
      color: var(--text-secondary);
      text-transform: uppercase;
      letter-spacing: 0.5px;
    }
  }

  tbody {
    .user-row {
      border-bottom: 1px solid var(--border-color);
      transition: all 0.2s ease;
      cursor: pointer;

      &:hover {
        background: var(--bg-secondary);
        transform: translateX(4px);
      }

      &.me {
        background: rgba(102, 126, 234, 0.05);
      }

      td {
        padding: 16px;
        font-size: 14px;
        color: var(--text-primary);
      }

      .user-name {
        .name-cell {
          display: flex;
          align-items: center;
          gap: 12px;

          .avatar {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background: var(--accent-primary);
            color: white;
            display: flex;
            align-items: center;
            justify-content: center;
            font-weight: 600;
            font-size: 16px;
          }

          .me-badge {
            color: var(--accent-primary);
            font-size: 18px;
            width: 18px;
            height: 18px;
          }
        }
      }

      .role-badge {
        display: inline-block;
        padding: 4px 12px;
        border-radius: 12px;
        font-size: 12px;
        font-weight: 600;
        
        &.role-student {
          background: #e3f2fd;
          color: #1976d2;
        }

        &.role-teacher {
          background: #f3e5f5;
          color: #7b1fa2;
        }

        &.role-admin {
          background: #fff3e0;
          color: #f57c00;
        }

        &.role-super_admin {
          background: #ffebee;
          color: #c62828;
        }
      }

      .status-badge {
        display: inline-block;
        padding: 4px 12px;
        border-radius: 12px;
        font-size: 12px;
        font-weight: 600;

        &.status-active {
          background: #e8f5e9;
          color: #2e7d32;
        }

        &.status-inactive {
          background: #fafafa;
          color: #757575;
        }

        &.status-deleted {
          background: #ffebee;
          color: #c62828;
        }
      }

      .actions-cell {
        display: flex;
        gap: 8px;

        .action-btn {
          background: transparent;
          border: 1px solid var(--border-color);
          border-radius: 6px;
          width: 32px;
          height: 32px;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          transition: all 0.2s ease;

          mat-icon {
            font-size: 18px;
            width: 18px;
            height: 18px;
            color: var(--text-secondary);
          }

          &:hover {
            background: var(--bg-secondary);
            border-color: var(--accent-primary);

            mat-icon {
              color: var(--accent-primary);
            }
          }

          &.delete:hover {
            background: #ffebee;
            border-color: #c62828;

            mat-icon {
              color: #c62828;
            }
          }
        }
      }
    }
  }
}

// 반응형
@media (max-width: 1200px) {
  .users-table {
    font-size: 13px;

    thead th,
    tbody td {
      padding: 12px 8px;
    }
  }
}

@media (max-width: 768px) {
  .filter-tabs {
    .tab-btn {
      padding: 8px 16px;
      font-size: 12px;

      mat-icon {
        font-size: 18px;
        width: 18px;
        height: 18px;
      }
    }
  }

  .users-table {
    display: block;
    overflow-x: auto;

    thead {
      display: none;
    }

    tbody {
      display: block;

      .user-row {
        display: block;
        margin-bottom: 16px;
        border: 1px solid var(--border-color);
        border-radius: 8px;
        padding: 16px;

        td {
          display: flex;
          justify-content: space-between;
          padding: 8px 0;
          border-bottom: none;

          &::before {
            content: attr(data-label);
            font-weight: 600;
            color: var(--text-secondary);
          }
        }

        .actions-cell {
          justify-content: flex-end;
        }
      }
    }
  }
}

// 주문 관리 탭 스타일
