  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 2px 8px var(--shadow);
}

.orders-table {
  width: 100%;
  border-collapse: collapse;

  thead {
    background: var(--bg-secondary);
    
    tr {
      border-bottom: 2px solid var(--border-color);
    }

    th {
      padding: 18px 16px;
      text-align: left;
      font-size: 13px;
      font-weight: 600;
      color: var(--text-secondary);
      text-transform: uppercase;
