          }
        }
      }

      // 결제방식
      .payment-method {
        .payment-method {
          display: inline-flex;
          align-items: center;
          gap: 6px;
          padding: 6px 12px;
          border-radius: 8px;
          font-size: 13px;
          font-weight: 600;
          transition: all 0.2s ease;

          mat-icon {
            font-size: 16px;
            width: 16px;
            height: 16px;
          }

          &.method-vbank {
            background: #e3f2fd;
            color: #1976d2;
            border: 1px solid #bbdefb;
          }

          &.method-card {
            background: #f3e5f5;
            color: #7b1fa2;
            border: 1px solid #e1bee7;
          }

          &.method-kakaopay {
            background: #fff9c4;
            color: #f57f17;
            border: 1px solid #fff59d;
          }

          &.method-bankTransfer {
            background: #e0f2f1;
            color: #00695c;
            border: 1px solid #b2dfdb;
          }
        }
      }

      // 주문 상태
      .order-status {
        .order-status {
          display: inline-flex;
          align-items: center;
          gap: 6px;
          padding: 6px 12px;
          border-radius: 8px;
          font-size: 13px;
          font-weight: 600;
          transition: all 0.2s ease;

          mat-icon {
            font-size: 16px;
            width: 16px;
            height: 16px;
          }

          &.status-pending {
            background: #fff3e0;
            color: #f57c00;
            border: 1px solid #ffe0b2;
          }

          &.status-paid {
            background: #e8f5e9;
            color: #2e7d32;
            border: 1px solid #c8e6c9;
          }

          &.status-cancelled {
            background: #ffebee;
            color: #c62828;
            border: 1px solid #ffcdd2;
          }

          &.status-draft {
            background: #fafafa;
            color: #757575;
            border: 1px solid #eeeeee;
          }

          &.status-failed {
            background: #ffebee;
            color: #d32f2f;
            border: 1px solid #ffcdd2;
          }

          &.status-shipped {
            background: #e1f5fe;
            color: #0277bd;
            border: 1px solid #b3e5fc;
          }

          &.status-delivered {
            background: #e8f5e9;
            color: #1b5e20;
            border: 1px solid #c8e6c9;
