          display: flex;
          gap: 8px;
          justify-content: center;
          align-items: center;

          button {
            padding: 8px 14px;
            font-size: 13px;
            border-radius: 6px;
            border: none;
            cursor: pointer;
            transition: all 0.2s ease;
            display: inline-flex;
            align-items: center;
            gap: 6px;
            font-weight: 500;
            white-space: nowrap;

            mat-icon {
              font-size: 18px;
              width: 18px;
              height: 18px;
            }

            &.btn-confirm {
              background: linear-gradient(135deg, #4caf50 0%, #388e3c 100%);
              color: white;
              box-shadow: 0 2px 8px rgba(76, 175, 80, 0.3);

              &:hover {
                transform: translateY(-2px);
                box-shadow: 0 4px 12px rgba(76, 175, 80, 0.4);
              }
