            &.btn-detail {
              background: var(--bg-secondary);
              color: var(--text-primary);
              border: 1px solid var(--border-color);
              padding: 8px;

              mat-icon {
                margin: 0;
              }

              &:hover {
                background: var(--bg-tertiary, #f5f5f5);
                border-color: var(--accent-primary);
                color: var(--accent-primary);
                transform: translateY(-2px);
              }
            }
          }
        }

        &.text-center {
          text-align: center;
        }
      }
    }
  }
}

