      &.date-header {
        min-width: 140px;
      }
      
      &.actions-header {
        min-width: 140px;
        text-align: right;
      }
    }
  }
}

// 브랜드 관리 전용 스타일
.store-logo {
  width: 64px;
  height: 64px;
  border-radius: 12px;
  overflow: hidden;
  background: #f8f9fa;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  border: 1px solid #e0e0e0;
  flex-shrink: 0;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    display: block;
  }
}

.store-info {
  display: flex;
  flex-direction: column;
  gap: 6px;
  max-width: 350px;

  .store-name {
    font-weight: 700;
    font-size: 15px;
    color: var(--text-primary);
    line-height: 1.4;
  }

  .store-desc {
    font-size: 13px;
    color: var(--text-secondary);
    line-height: 1.5;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}

.owner-info {
  font-size: 14px;
  color: var(--text-primary);
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 200px;
}

.product-count {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 14px;
  font-weight: 500;
  color: var(--text-primary);
  background: var(--bg-secondary);
  padding: 6px 12px;
  border-radius: 20px;
  width: fit-content;

  mat-icon {
    font-size: 16px;
    width: 16px;
    height: 16px;
    color: var(--text-secondary);
  }
  
  span {
    line-height: 1;
  }
}

// 브랜드 테이블 전용 행 스타일
.store-row {
  border-bottom: 1px solid var(--border-color);
  transition: background-color 0.2s ease;
  
  &:hover {
    background-color: var(--bg-hover, #f8f9fa);
  }
  
  td {
    padding: 16px;
    vertical-align: middle;
    
    &.store-logo-cell {
      width: 80px;
      padding: 12px;
    }
    
    &.store-name-cell {
      min-width: 250px;
    }
    
    &.owner-cell {
      min-width: 150px;
    }
    
    &.product-cell {
      min-width: 100px;
    }
    
    &.status-cell {
      min-width: 100px;
    }
    
    &.date-cell {
      min-width: 140px;
      font-size: 13px;
      color: var(--text-secondary);
    }
    
    &.actions-cell {
      min-width: 140px;
      text-align: right;
    }
  }
}

.status-badge {
  display: inline-flex;
  align-items: center;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;

  &.status-active {
    background: #e8f5e9;
    color: #2e7d32;
    border: 1px solid #c8e6c9;
  }

  &.status-inactive {
    background: #f5f5f5;
    color: #616161;
    border: 1px solid #e0e0e0;
  }

  &.status-deleted {
    background: #ffebee;
    color: #c62828;
    border: 1px solid #ffcdd2;
  }

  &.status-unknown {
    background: #eceff1;
    color: #546e7a;
    border: 1px solid #cfd8dc;
  }
}

// 액션 버튼 스타일 개선
.actions-group {
  display: flex;
  gap: 8px;

  button {
    width: 36px;
    height: 36px;
    border-radius: 8px;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2s cubic-bezier(0.4, 0, 0.2, 1);
    color: white;

    mat-icon {
      font-size: 20px;
      width: 20px;
      height: 20px;
    }

    &:hover {
      transform: translateY(-2px);
      filter: brightness(1.1);
    }

    &:active {
      transform: translateY(0);
    }
  }

  .btn-edit {
    background: linear-gradient(135deg, #2196f3 0%, #1976d2 100%);
    box-shadow: 0 2px 6px rgba(33, 150, 243, 0.3);
  }

  .btn-toggle {
    background: linear-gradient(135deg, #ff9800 0%, #f57c00 100%);
    box-shadow: 0 2px 6px rgba(255, 152, 0, 0.3);
  }

  .btn-delete {
    background: linear-gradient(135deg, #f44336 0%, #d32f2f 100%);
    box-shadow: 0 2px 6px rgba(244, 67, 54, 0.3);
  }
}
